@charset "utf-8";

:root{
	--mainEng-font:"Montserrat";
	--mainKor-font:"nanumSquareNeo";
	--mainNum-font:"gmarket";

	--mainBg-color : #f3ede8;
	--subBg100:#cdc0b1;
	--subBg200:#afa395;
	--subBg300:#81887c;
	--subBg400:#afa7a2;
	--subBg500:#a6afa2;

	--white:#fff;
	--black:#000;
	--black100:#2b2b2b;
	--black200:#434343;
	--black300:#686868;
	--black400:#e0e0e0;

	/*기본 폰트 설정*/
	font-family: var(--mainEng-font), var(--mainKor-font);
	font-size: 16px;
	line-height: 1.5;
	font-weight: 400;


	font-synthesis: none;
	/*bold, italic을 못쓰게 막아놓는것을 해제 하는 명령어 , 브라우저속성값에서*/
	-webkit-font-smoothing: antialiased; /*최적화*/

	/*
	  -webkit- : 구글, 사파리 브라우저에 적용
	  -moz- : 파이어폭스 브라우저에 적용
	  -ms- : 익스플로러에 적용, 보통 생략합니다.
	  -o- : 오페라 브라우저에 적용
	*/

	/* 아이폰 가로 모드에서 글씨 확대 금지 */
	-webkit-text-size-adjust: 100%;

}
@media(max-width:800px){
	:root{
		font-size: 14px;
		line-height: 1.4;
	}
}

body{
	color: var(--black100);
	background-color: var(--mainBg-color);
}